export default{
	data(){
		return{
			form:{
				longitude:null,
				latitude:null,
				schoolName:"",
				schoolId:""
			},
			address:''
			
		}
	},
	methods:{
		gomap(){
			//地图定位
			uni.chooseLocation({
				success:(res)=>{
					console.log(res)
					this.form.longitude=res.longitude
					this.form.latitude=res.latitude
					this.address=res.address
				}
			})
		},
		goRouter(){
			if(this.form.schoolName&&this.form.longitude){
				let data=this.form
				data=JSON.stringify(data)
				uni.setStorageSync("addchildinfo",data)
				uni.navigateTo({
					url:"addchildmsg"
				})
			}else{
				uni.showToast({
					icon:"none",
					title:"请填写学校名称及学校定位信息！"
				})
			}
			
		}
	}
}
